/* animations.scss */

// Animaciones para uso global en la aplicación
// Importar este archivo en los componentes que necesiten usar estas animaciones

// Animación de entrada suave
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  // Animación de entrada desde la derecha
  @keyframes slideInFromRight {
    from { 
      transform: translateX(20px); 
      opacity: 0; 
    }
    to { 
      transform: translateX(0); 
      opacity: 1; 
    }
  }
  
  // Animación de entrada desde la izquierda
  @keyframes slideInFromLeft {
    from { 
      transform: translateX(-20px); 
      opacity: 0; 
    }
    to { 
      transform: translateX(0); 
      opacity: 1; 
    }
  }
  
  // Animación de entrada desde abajo
  @keyframes slideUpFade {
    from { 
      transform: translateY(20px); 
      opacity: 0; 
    }
    to { 
      transform: translateY(0); 
      opacity: 1; 
    }
  }
  
  // Animación de entrada desde arriba
  @keyframes slideDownFade {
    from { 
      transform: translateY(-20px); 
      opacity: 0; 
    }
    to { 
      transform: translateY(0); 
      opacity: 1; 
    }
  }
  
  // Animación de rebote
  @keyframes bounceIn {
    0%, 20%, 40%, 60%, 80%, 100% { 
      transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); 
    }
    0% { 
      opacity: 0; 
      transform: scale3d(0.3, 0.3, 0.3); 
    }
    20% { 
      transform: scale3d(1.1, 1.1, 1.1); 
    }
    40% { 
      transform: scale3d(0.9, 0.9, 0.9); 
    }
    60% { 
      opacity: 1; 
      transform: scale3d(1.03, 1.03, 1.03); 
    }
    80% { 
      transform: scale3d(0.97, 0.97, 0.97); 
    }
    100% { 
      opacity: 1; 
      transform: scale3d(1, 1, 1); 
    }
  }
  
  // Animación de efecto flotante
  @keyframes float {
    0%, 100% { 
      transform: translateY(0); 
    }
    50% { 
      transform: translateY(-10px); 
    }
  }
  
  // Animación de pulso
  @keyframes pulse {
    0% { 
      transform: scale(1); 
    }
    50% { 
      transform: scale(1.05); 
    }
    100% { 
      transform: scale(1); 
    }
  }
  
  // Animación de parpadeo
  @keyframes blink {
    0%, 100% { 
      opacity: 1; 
    }
    50% { 
      opacity: 0.5; 
    }
  }
  
  // Animación de rotación
  @keyframes spin {
    from { 
      transform: rotate(0deg); 
    }
    to { 
      transform: rotate(360deg); 
    }
  }
  
  // Animación de efecto escalofriante para errores
  @keyframes shake {
    0%, 100% { 
      transform: translateX(0); 
    }
    10%, 30%, 50%, 70%, 90% { 
      transform: translateX(-5px); 
    }
    20%, 40%, 60%, 80% { 
      transform: translateX(5px); 
    }
  }
  
  // Animación de borde pulsante
  @keyframes pulseBorder {
    0%, 100% { 
      border-color: rgba(67, 97, 238, 0.3); 
    }
    50% { 
      border-color: rgba(67, 97, 238, 0.8); 
    }
  }
  
  // Animación para efecto de shimmer (carga)
  @keyframes shimmer {
    0% {
      background-position: -1000px 0; 
    }
    100% { 
      background-position: 1000px 0; 
    }
  }
  
  // Efecto de desvanecimiento para transiciones entre páginas
  @keyframes crossfade {
    0%, 100% { 
      opacity: 0; 
    }
    50% { 
      opacity: 1; 
    }
  }
  
  // Efecto de tipeo
  @keyframes typing {
    from { 
      width: 0; 
    }
    to { 
      width: 100%; 
    }
  }
  
  // Clases para aplicar animaciones fácilmente
  .animate {
    &-fade-in {
      animation: fadeIn 0.5s ease forwards;
    }
    
    &-slide-right {
      animation: slideInFromRight 0.4s ease forwards;
    }
    
    &-slide-left {
      animation: slideInFromLeft 0.4s ease forwards;
    }
    
    &-slide-up {
      animation: slideUpFade 0.5s ease forwards;
    }
    
    &-slide-down {
      animation: slideDownFade 0.5s ease forwards;
    }
    
    &-bounce {
      animation: bounceIn 0.7s ease forwards;
    }
    
    &-float {
      animation: float 5s ease-in-out infinite;
    }
    
    &-pulse {
      animation: pulse 2s ease-in-out infinite;
    }
    
    &-spin {
      animation: spin 1s linear infinite;
    }
    
    &-shake {
      animation: shake 0.5s ease-in-out;
    }
    
    &-blink {
      animation: blink 1.5s ease-in-out infinite;
    }
  }
  
  // Modificadores de animación
  .delay {
    &-100 {
      animation-delay: 100ms;
    }
    
    &-200 {
      animation-delay: 200ms;
    }
    
    &-300 {
      animation-delay: 300ms;
    }
    
    &-500 {
      animation-delay: 500ms;
    }
    
    &-1000 {
      animation-delay: 1000ms;
    }
  }
  
  // Mixins de animación para uso en otros archivos SCSS
  @mixin fade-in($duration: 0.5s, $delay: 0s) {
    animation: fadeIn $duration ease forwards;
    animation-delay: $delay;
  }
  
  @mixin slide-up($duration: 0.5s, $delay: 0s) {
    animation: slideUpFade $duration ease forwards;
    animation-delay: $delay;
  }
  
  @mixin float-animation($duration: 5s, $distance: 10px) {
    animation: float $duration ease-in-out infinite;
    
    @keyframes float {
      0%, 100% { transform: translateY(0); }
      50% { transform: translateY(-$distance); }
    }
  }
  
  @mixin pulse-animation($duration: 2s) {
    animation: pulse $duration ease-in-out infinite;
  }
  
  // Animación de carga para botones o elementos pendientes
  @mixin loading-dots {
    position: relative;
    
    &::after {
      content: '';
      animation: loadingDots 1.5s infinite;
    }
    
    @keyframes loadingDots {
      0% { content: '.'; }
      33% { content: '..'; }
      66% { content: '...'; }
    }
  }